<template>
    <div class="bg-box">
        <div class="title"><p class="title-content">抽奖平台</p></div>
        <div class="content">
            <div class="set-box">
            <p class="set-title">设置</p>
              <p> <span class="label">首次预期抽奖人数：</span><a-input placeholder="请输入首次预期抽奖人数" @change="getPridectNum" style="width:200px;" /></p>
               
                 <p> <span class="label">首次最大中奖人数：</span><a-input placeholder="请输入最大中奖人数" @change="getMaxWinNum " style="width:200px;" /></p>
                  <p > <span class="label">首次中奖基数：</span><a-input placeholder="请输入首次中奖基数" @change="getBaseWinNum" style="width:200px;" /></p>
                 <a-button type="primary" class="btn-save" @click="save">保存</a-button>
        </div>
        <div class="lottery-box">
             <p class="set-title">抽奖</p>
              
             <p class="lottery-detail">当前轮次抽奖人数：{{peopleNum}}</p>
            <p class="lottery-detail">当前轮次中奖人数：{{winPeopleNum}}</p>
             <div class="right-top-btn animat" @click="startLottery">
               <span  class="cj">抽奖</span>
                <!-- <transition   enter-active-class="animated bounceIn" leave-active-class="animated bounceOut" :duration="200"><span v-if="flag">抽奖</span></transition> -->
                  <!-- <transition enter-active-class="**animated** bounceIn" leave-active-class="**animated** bounceOut" :duration="200">
      <span @click="startLottery">抽奖</span>
    </transition> -->
               <!-- <a-button type="primary" @click="startLottery"></a-button> -->
            </div> 
        </div>
        <div class="left-btm">
             <div class="view-box">
            <p class="set-title">当前参数</p>
             <p class="labels"><span >预期抽奖次数：</span>{{getpredictNum}}</p>
               <p class="labels"><span >获奖基数：</span>{{getBaseWin}}</p>
               <p class="labels"> <span >最大获奖人数：</span>{{getMaxWin}}</p>
               <p class="labels"><span >当前轮次：</span>{{round}}</p>
               <p class="labels"><span >当前中奖人数：</span>{{winPeopleNum}}</p>
              
               
                 <!-- <p> <span class="label">首次最大中奖人数：</span><a-input placeholder="请输入最大中奖人数" @change="getMaxWinNum " style="width:200px;" /></p>
                  <p > <span class="label">首次中奖基数：</span><a-input placeholder="请输入首次中奖基数" @change="getBaseWinNum" style="width:200px;" /></p>
                 <a-button type="primary" class="btn-save" @click="save">保存</a-button> -->
        </div>
        <!-- <div class="next-rounds" >
             <p class="set-title">查看参数</p>
            <p  class="next-round" @click="nextRound">下一轮</p>
           
        </div> -->
        <div class="next-round"  @click="nextRound">
            <p>下一轮</p>
        </div>
        </div>
        <div class="right-btm">
             <span class="set-title-record">抽奖记录</span>
              <a-table :columns="columns" :data-source="dataSource">
                 <span slot="maxWinNum" slot-scope="text,record">
                   {{Number(record.maxWinNum)-Number(record.winPeopleNum)}}
                 </span>
             </a-table>
        </div>
       <div class="mask" v-if="isShow">
        
         <div class="mask-content">
            <a-icon type="sync" spin v-if="isLoading" style="font-size:64px;color:#1890ff"/>
            <a-icon class="icon-box" v-if="(isSuccess&& !isLoading) ||(isFail&& !isLoading) " type="close-circle" @click="close"/>
            <img v-if="isSuccess && !isLoading" src="../../static/success.jpg" alt="" style="width:260px;height:195px;">
            <img v-if="isFail && !isLoading" src="../../static/sorry.png" alt="" style="width:281px;height:242px;">
         </div>
       </div>
        </div>
    </div>

</template>
<script>
import animate from "animate.css";
const api = "http://10.45.53.111:9099";
export default {
  data() {
    return {
      predictNum: 0,
      baseWinNum: 0,
      maxWinNum: 0,
      peopleNum: 0,
      winPeopleNum: 0,
      round: 0,
      getBaseWin: 0,
      getMaxWin: 0,
      getpredictNum:0,
      dataSource: [],
      isShow: false,
      isSuccess:false,
      isFail:false,
      isLoading:false,
      columns: [
        {
          title: "预期数",
          dataIndex: "predictNum",
          key: "predictNum"
        },
        {
          title: "抽奖数",
          dataIndex: "peopleNum",
          key: "peopleNum"
        },
        {
          title: "中奖基数",
          dataIndex: "baseWinNum",
          key: "baseWinNum"
        },
        // {
        //   title: "最大中奖数",
        //   dataIndex: "max_win_num",
        //   key: "max_win_num"
        // },
        {
          title: "中奖数",
          dataIndex: "winPeopleNum",
          key: "winPeopleNum"
        },
        {
          title: "轮数",
          dataIndex: "round",
          key: "round"
        },
        {
          title: "剩余奖品",
          dataIndex: "maxWinNum",
          key: "maxWinNum",
          scopedSlots: { customRender: "maxWinNum" }
        }
        //  {
        //     title: '中奖数',
        //     dataIndex: 'win_people_num',
        //     key: 'win_people_num',
        //     width: 80,
        // },
      ]
    };
  },
  mounted() {
    this.getLotteryData();
    this.lotteryData();
  },
  methods: {
    close() {
      this.isShow = false;
      this.isSuccess = false;
    },
    callback() {},
    // 设置参数
    getPridectNum(e) {
      // console.log(e,e.target.value)
      this.predictNum = e.target.value;
    },
    getMaxWinNum(e) {
      this.maxWinNum = e.target.value;
    },
    getBaseWinNum(e) {
      this.baseWinNum = e.target.value;
    },
    save() {
      this.$axios({
        url: api + "/api/param/initFirstDayParam",
        method: "get",
        params: {
          predictNum: this.predictNum,
          baseWinNum: this.baseWinNum,
          maxWinNum: this.maxWinNum
        }
      }).then(res => {
        if (res.data.code == 200) {
          console.log(res.data);
          this.$confirm({
            content:'参数保存成功',
           okType:'success',
            okText:'确定',
            width:300,
            cancelText:'取消',
            onOk:()=>{
              this.getLotteryData();
            },
            onCancel:()=>{
              this.getLotteryData();
            }
          })
          
        }
      });
    },
    // 抽奖
    startLottery() {
      
      this.isShow = true;
      this.isLoading = true;
      this.$axios({
        url: api + "/api/lottery/play",
        method: "get",
        params: {}
      }).then(res => {
        if (res.data.code == 200) {
          this.isLoading = false;
          if(res.data.data==1){
            this.isLoading = false;
            this.isSuccess = true;
           this.isFail = false;
          }else{
            this.isLoading = false;
            this.isSuccess = false;
          this.isFail = true;
          }
           
          // this.$message.success(res.data.message);
          this.getLotteryData();
          thuis.lotteryData();
         

          // this.confirm({
          //     confirmButtonText: '确定',
          //     cancelButtonText: '取消',
          //     type: 'warning'
          // })
        }else{
          
        }
      });
    },
    // 获取中奖信息
    getLotteryData() {
      this.$axios({
        url: api + "/api/param/queryParam",
        method: "get",
        params: {}
      }).then(res => {
        console.log(res.data, "res");
        if (res.data.code == 200) {
          this.peopleNum = res.data.data.peopleNum;
          this.getpredictNum = res.data.data.predictNum;
          this.winPeopleNum = res.data.data.winPeopleNum;
          this.round = res.data.data.round;
          this.getMaxWin = res.data.data.maxWinNum;
          this.getBaseWin = res.data.data.baseWinNum;
        }
      });
    },
    nextRound() {
      this.$axios({
        url: api + "/api/param/nextLottery",
        method: "get",
        params: {}
      }).then(res => {
        if (res.data.code == 200) {
          this.getLotteryData();
        }
      });
    },
    // 抽奖记录
    lotteryData() {
      this.$axios({
        url: api + "/api/param/queryResult",
        method: "get",
        params: {}
      }).then(res => {
        if (res.data.code == 200) {
          this.dataSource = res.data.data;
        }
      });
    }
  }
};
</script>
<style scoped>
.bg-box {
  width: 100%;
  background: rgb(25, 29, 41);
}
.title {
  width: 100%;
  background: rgb(41, 45, 57);
}
.title-content {
  width: 80%;
  margin: 0 auto;
  height: 60px;
  line-height: 60px;

  color: #fff;
  text-align: left;
  font-size: 20px;
}
.content {
  color: #fff;
  width: 80%;
  /* border: 1px solid #afa; */
  margin: 0 auto;
  margin-top: 30px;
  height: 100%;
}
.set-box {
  width: 40%;
  height: 300px;
  background: rgb(41, 45, 57);
  display: inline-block;
}

.set-title {
  width: 100%;
  height: 32px;
  line-height: 32px;
  border-bottom: 1px solid rgb(139, 143, 165);
}
.set-title p {
  margin-bottom: 20px;
}
.btn-save {
  width: 81%;
  margin: 0 auto;
  margin-top: 30px;
  display: inline-block;
}
.label {
  width: 130px;
  display: inline-block;
  text-align: left;
  /* border:1px solid red; */
}
.lottery-box {
  margin-left: 10%;
  width: 40%;
  height: 300px;
  background: rgb(41, 45, 57);
  display: inline-block;
  vertical-align: top;
}
.lottery-detail {
  text-align: left;
  margin-left: 40px;
}
.right-top-btn {
  width: 150px;
  height: 150px;
  background: red;
  color: #fff;
  border-radius: 50%;
  margin: 0 auto;
  line-height: 150px;
  font-size: 24px;
  font-weight: 600;
  cursor: pointer;
}
.left-btm {
  width: 40%;
  display: inline-block;
  /* border:1px solid orange */
}
.view-box {
  width: 100%;
  height: 250px;
  background: rgb(41, 45, 57);
  /* display: inline-block; */
  margin-top: 30px;
  /* float: left; */
  /* margin-left: 20px; */
}
.labels {
  text-align: left;
  margin-left: 40px;
}
.next-rounds {
  width: 100%;
  height: 150px;
  background: rgb(41, 45, 57);
  /* display: inline-block; */
  margin-top: 30px;
}
.next-round {
  width: 200px;
  /* width:100px; */
  height: 60px;
  border-radius: 10px;
  line-height: 60px;
  margin: 0 auto;
  margin-top: 50px;
  color: #fff;
  background: rgb(3, 149, 255);
  cursor: pointer;
}
.set-title-record {
  background: rgb(41, 45, 57);
  width: 100%;
  height: 32px;
  line-height: 32px;
  border-bottom: 1px solid rgb(139, 143, 165);
  display: block;
}
.right-btm {
  width: 40%;
  margin-left: 10%;
  height:400px;
  overflow-y:auto;
  background:rgb(41, 45, 57);
  color:#fff;
  /* border:1px solid red; */
  display: inline-block;
  height: 100%;
  vertical-align: top;
  margin-top: 30px;
}
.mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2;
}
.mask-content {
  border-radius: 15px;
  text-align: center;
  width: 20%;
  height: 20%;
  position: absolute;
  top: 20%;
  left: 40%;
  /* top: 0;
    right: 0;
    bottom: 0;
    left: 0; */
  margin: auto;
  /* background-color: rgb(252,118,3); */
}
.icon-box {
  top: -30px;
  right: -30px;
  position: absolute;
  font-size: 28px;
}

.animat {
  position: relative;
  animation: mymove 5s infinite;
  -webkit-animation: mymove 5s infinite; /*Safari and Chrome*/
  animation-direction: alternate; /*轮流反向播放动画。*/
  animation-timing-function: ease-in-out; /*动画的速度曲线*/
  /* Safari 和 Chrome */
  -webkit-animation: mymove 5s infinite;
  -webkit-animation-direction: alternate; /*轮流反向播放动画。*/
  -webkit-animation-timing-function: ease-in-out; /*动画的速度曲线*/
}
@keyframes mymove {
  0% {
    transform: scale(1); /*开始为原始大小*/
  }
  25% {
    transform: scale(1.1); /*放大1.1倍*/
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(1.1);
  }
}

@-webkit-keyframes mymove /*Safari and Chrome*/ {
  0% {
    transform: scale(1); /*开始为原始大小*/
  }
  25% {
    transform: scale(1.1); /*放大1.1倍*/
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(1.1);
  }
}
/* .animat:hover{
              transform:scale(1.5)
            } */
.cj:hover {
  font-size: 26px;
  transform: scale(3);
}
</style>
<style>
.ant-table-thead > tr > th {
  color: #fff !important;
  background: rgb(41, 45, 57) !important;
}
.ant-table-placeholder {
  border-bottom: none !important;
  color: #fff !important;
  background: rgb(41, 45, 57) !important;
  border-top: 1px solid rgb(139, 143, 165) !important;
  border-radius:none !important;
}
.ant-empty-normal{
  color:#fff !important;
}
.ant-table-thead > tr:first-child > th:first-child{
  border-top-left-radius:0 0 0 0 !important;
}
.ant-table-thead > tr > th, .ant-table-tbody > tr > td{
  background:rgb(41, 45, 57) !important;
  color:#fff !important;
}
.ant-pagination-prev .ant-pagination-item-link, .ant-pagination-next .ant-pagination-item-link{
  background:transparent !important;
}
.ant-pagination-disabled a, .ant-pagination-disabled:hover a, .ant-pagination-disabled:focus a, .ant-pagination-disabled .ant-pagination-item-link, .ant-pagination-disabled:hover .ant-pagination-item-link, .ant-pagination-disabled:focus .ant-pagination-item-link{
  color:#fff !important;
}
.ant-pagination-item{
  background:transparent !important;
}
</style>






